function App() {
  const [currentLibraryArticle, setCurrentLibraryArticle] = useState();
  const [inheritedLibraryArticles, setInheritedLibraryArticles] = useState();

  window.onpopstate = () => {
    setCurrentLibraryArticle();
    setInheritedLibraryArticles();
  }

  return (
    <div className="App">
      <Router> {/* history={history} */}
        <ScrollTo />
        <Header />
        <FloatingContact />
        <Switch>
          <Route path="/case-studies/:projectName">
            <CaseStudypage />
          </Route>
          <Route path="/library/:articleName">
            <LibraryArticlePage 
              inheritedArticle={currentLibraryArticle} 
              inheritedAllArticles={inheritedLibraryArticles} 
              setCurrentLibraryArticle={setCurrentLibraryArticle}
            />
          </Route>
          <Route path="/library">
            <LibraryPage setCurrentLibraryArticle={setCurrentLibraryArticle} setInheritedLibraryArticles={setInheritedLibraryArticles}/>
          </Route>
          {/* <Route path="/about">
            <Aboutpage />
          </Route> */}
          <Route path="/:scrollTo">
            <Homepage />
          </Route>
          <Route path="/">
            <Homepage />
          </Route>
        </Switch>
        <Footer />
      </Router>
    </div>
  );
}

export default App;

