import React, {useEffect, useState} from 'react';
import {Link, useParams} from 'react-router-dom';
import {htmlToText} from 'html-to-text';

import {GApageView} from '../analytics';
import {getDisplayDate} from '../utils/date';
import logo from '../assets/logo_254.png';

export default function LibraryArticlePage({
    inheritedArticle,
    inheritedAllArticles,
    setCurrentLibraryArticle,
}){
    console.log(inheritedArticle);
    const {articleName} = useParams();

    const [article, setArticle] = useState(inheritedArticle || {});
    const [allArticles, setAllArticles] = useState(inheritedAllArticles || []);
    const [currentTags, setCurrentTags] = useState(
        inheritedArticle 
            ? inheritedArticle.tags
            : []
    );

    useEffect(()=>{
        GApageView("/library/" + articleName);
    }, []);

    useEffect(()=>{
        if(inheritedArticle)
            setArticle(inheritedArticle);
    }, [inheritedArticle])

    useEffect(()=>{
        const articleTitle = articleName.replace(/-/g,' ');
        console.log(articleTitle, inheritedArticle);
        const out = [];
        if(!inheritedArticle && articleName && articleName.length){
            global.db.collection('CMS')
                .doc('webbi_co_nz')
                .collection('Library')
                // .doc(articleName)
                .get()
                .then((snapshot) => {
                    for(var doc of snapshot.docs){//(doc => {
                        console.log(doc)
                        console.log(htmlToText(doc.get('title')).toLowerCase())
                        const obj = {id: doc.id, ...doc.data()};
                        const matches = new RegExp(/TAGS:[ ]*(.+)/g).exec(htmlToText(obj.content));
                        if(matches && matches.length) obj.tags = matches[1].split(',');
                        console.log(obj);
                        if(htmlToText(doc.get('title')).toLowerCase() === articleTitle){
                            setArticle(obj);
                            setCurrentTags(obj.tags);
                        }
                        // else {
                            out.push(obj);
                        // }
                    // })
                // })
                // .then((doc)=>{
                        
                    };
                    setAllArticles(out);
                })
                .catch(err => {console.error("Error retrieving article information: ", err)});
        }
    }, [articleName]);
    
    return(
        <div className="LibraryArticlePage">
            <div className="wrapper">
                <div className="LibraryArticleItem">
                    <div className="tags">
                        { (article.tags || []).map((tag, i) => (
                            <div className="tag" key={"tag-"+i}>
                                {tag}
                            </div>
                        ))
                        }
                    </div>
                    <div className="title">
                        {htmlToText(article.title)}
                    </div>
                    { article.date 
                        ? <div className="date">{getDisplayDate(article.date.seconds)}</div>
                        : <></>
                    }

                    {
                        article.images && article.images.length
                        ? <img src={article.images[0].url} alt="" />
                        : <></>
                    }
                    
                    <div className="content" dangerouslySetInnerHTML={{__html: (article.content || "").replace(/TAGS:[ ]*(.+)/,'')}}></div>
                </div>
                {/* <div className="sticky-wrapper"> */}
                    <OtherLinks 
                        articleName={articleName} 
                        allArticles={allArticles} 
                        currentTags={currentTags} 
                        setCurrentLibraryArticle={setCurrentLibraryArticle}
                    />
                {/* </div> */}
            </div>
            <InlineCta />
        </div>
    )
}

const OtherLinks = ({
    allArticles,
    currentTags,
    articleName,
    setCurrentLibraryArticle,
}) => {
    // const filteredTags = allArticles.filter((article) => {
    //     article.tags.filterincludes()
    // })
    // console.log(currentTags)
    console.log("articleName: ", articleName);
    return(
        <div className="OtherLinks">
            {/* <h2>Related articles</h2> */}
            <h2>Other articles</h2>
            <div className="content-wrapper">
                <ul>
                {
                    (allArticles || []).map((article, i)=>(
                        
                        articleName.replace(/-/g,' ').toLowerCase() !== htmlToText(article.title).toLowerCase()
                            ? <li key={"other-article-"+i}>
                                <Link to={`/library/${htmlToText(article.title).replace(/[ ]/g,'-').toLowerCase()}`}
                                    onClick={()=>{
                                        setCurrentLibraryArticle(article);
                                    }}
                                >
                                    <div className="">
                                        {
                                            (article.tags || [])
                                            // .filter((tag) => {
                                            //     // currentTags.includes(tag.toLowerCase())
                                            //     var reg = new RegExp(currentTags.join('|').replace(/\s*/g,""), "i");
                                            //     // console.log(currentTags, tag, reg.test(tag));
                                            //     return reg.test(tag);
                                            // })
                                            .map((tag, i) => (
                                                <div key={"related-tag-"+i} 
                                                    className={"tag " + (new RegExp(currentTags.join('|').replace(/\s*/g,""), "i").test(tag) ? "" : "non-intersection")}>{tag}</div>
                                            ))
                                        }
                                    </div>
                                    <div className="title">{htmlToText(article.title)}</div>
                                </Link> 
                            </li>
                            : ""
                    ))
                }
                </ul>
            </div>
        </div>
    )
}

const InlineCta = () => {
    return(
        <div className="InlineCta">
            <img src={logo} alt="webbi.co.nz logo" />
            <Link to={`/contact`}>
                <span>Make an Inquiry</span>
            </Link>
        </div>
    )
}