import React, {useEffect, useState} from 'react';
import {Link} from 'react-router-dom';
import {htmlToText} from 'html-to-text';
import {GApageView} from '../analytics';
import {getDisplayDate} from '../utils/date';

export default function LibraryPage({
    setCurrentLibraryArticle,
    setInheritedLibraryArticles,
}){

    const [library, setLibrary] = useState([]);

    useEffect(()=>{
        GApageView("/library");
    }, []);

    useEffect(()=>{
        if(!global.db) return;
        const out = [];
        global.db.collection('CMS')
            .doc('webbi_co_nz')
            .collection('Library')
            .orderBy('date', 'desc')
            .get()
            .then(docs => {
                docs.forEach((doc)=>{
                    const obj = {id: doc.id, ...doc.data()};
                    const matches = new RegExp(/TAGS:[ ]*(.+)/g).exec(htmlToText(obj.content));
                    // obj.tags = htmlToText(obj.content).replace(/TAGS:[ ]*(.+)/,function(a, b){
                    //     console.log(a,b.replace(/[ ]/g,'').split(','))
                    //     return "hello there"//b.split(',');
                    // })
                    if(matches && matches.length)
                        obj.tags = matches[1].split(',');
                    // console.log(obj);
                    out.push(obj);
                })
                setLibrary(out);
            })
            .catch(err => {console.error("Error retrieving data: ", err)})
    }, []);

    return(
        <div className="LibraryPage">
            <div className="library-wrapper">
            {
                library.map((item, i) => (
                    <LibraryItem key={"library-item-"+i} item={item} library={library} setCurrentLibraryArticle={setCurrentLibraryArticle} setInheritedLibraryArticles={setInheritedLibraryArticles} />
                ))
            }
            </div>
            
        </div>
    )
}

const LibraryItem = ({item, library, setCurrentLibraryArticle, setInheritedLibraryArticles}) => {
    const currentTitle = htmlToText(item.title).replace(/[ ]/g,'-').toLowerCase();
    return(
        <Link className="link" to={"/library/" + currentTitle}
            onClick={()=>{
                setCurrentLibraryArticle(item);
                setInheritedLibraryArticles(library);
            }}
        >
            <div className="LibraryItem">
                <div className="left">
                    <div className="title">
                        {htmlToText(item.title)}
                    </div>
                    <div className="date">{getDisplayDate(item.date.seconds)}</div>
                    <div className="tags">
                        { (item.tags || []).map((tag, i) => (
                            <div className="tag" key={"tag-"+i}>
                                {tag}
                            </div>
                        ))
                        }
                    </div>
                    <div className="content">{htmlToText(item.content).replace(/TAGS:[ ]*(.+)/,'').substring(0, 200) + '...'}</div>
                </div>
                {/* <div className="content-wrapper"> */}
                <div className="right">
                    {
                        item.images && item.images.length
                        ? <img src={item.images[0].url} alt="" />
                        : <></>
                    }
                {/* </div> */}
                </div>
            </div>
        </Link>
    )
}